h1 {
  font-weight: bold;
}
.App:not(.prod) h1 {
  text-shadow: 2px 3px 5px navy;
}

samp {
  font-size: 1.3em;
  font-weight: 600;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App {
  flex-grow: 2;
  min-height: 100%;
}

#logo {
  display: block;
  float: right;
  max-width: 25%;
}
#logo img {
  width: 100%;
}

#site-footer {
  padding: 3em;
  text-align: center;
  background: #dabbff;
}
